div{
    display: block;
}
.authWindow{    
    width: 400px;
    color: white;
    font-size: 25px;
    background-color: rgb(43, 41, 49);
    border: 1px solid black;
    border-radius: 5px;
    box-shadow: 0 0 10px black;
    padding: 10px;
    box-sizing: border-box;
    position: absolute;
    top: calc(50% - 0px);
    left: calc(50% - 200px);
    padding-top: 20px;
    padding-bottom: 60px;
}
.buttonContainer{
    width: calc(100% - 20px);
    position: absolute;
    bottom: 10px;
}
.buttonContainerFlex{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
}
.closeButton{
    position: absolute;
    top: 5px;
    right: 5px;
    line-height: 15px;
    cursor: pointer;
    user-select: none;
}
.buttonWide{
    width: 45%;
    line-height: 30px;
}
.inputField{
    width: calc(100% - 20px);
    height: 30px;    
    margin: 10px;
    border: none;
    outline: none;
    background-color: rgb(192, 192, 192);
    border-radius: 3px;  
    padding: 0 5px 0 5px;
}